.sectionAbout {
    width: 100vw;
    height: 90vh;
    display: inline-flex;
    flex-direction: column;
    align-items: center;
    cursor: default;
}

.about_display {
    width: 100%;

    &_container {
        width: 100%;
    }

    &_content {
        display: inline-flex;
        align-items: center;
        margin-top: 50px;

        &_img {
            width: max(10vw, 250px);
            border-radius: 200px;
            padding: 10px;
            background-color: rgba(255, 255, 255, 0.64);
            box-shadow: 0 0 0 2px #ffc4c4, 0 0 0 5px white, 0 0 0 10px #ffc4c4;
        }

        &_links {
            display: flex;
            flex-direction: column;
            margin-left: 25px;
            line-height: 50px;
            text-align: left;
            text-decoration: none;

            &_name {
                padding: 0 20px 0 20px;
                color: #564B4B;
                text-decoration: none !important;

                &:hover {
                    color: #d68585;
                    transition: all 0.2s ease-in-out;
                    cursor: pointer;
                }
            }
        }
    }

    &_description {
        width: 100%;
        padding: 20px 0 20px 0;
        display: flex;
        justify-content: center;

        &_container {
            width: 47vw;
            padding: 10px;
            border: 2px solid #ffc4c4;
            border-radius: 15px;
            background-color: rgba(255, 255, 255, 0.64);
        }   

        &_content {
            width: 45vw;
            padding: 10px;
            font-size: max(1.2vw, 18px);
            text-align: center;
            background-color: #ffc4c4;
            border-radius: 10px;
            border: 1px solid #ffc4c4;
            font-family: 'Lato', sans-serif;
        }
    }
}

.about_diamond {
    &:before {
        margin-top: 20px;
        background-color: #ffc4c4;

        &:hover {
            background-color: #d68585;
        }
    }

    &_inner {
        &:before {
            margin-top: 10px;
        }
    }
}
