.sectionPrototype {
    width: 100vw;
    height: 90vh;
    display: inline-flex;
    flex-direction: column;
    align-items: center;
    cursor: default;
}

.prototypes_display {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    place-items: center;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 3vw;
    padding: 10px;
    overflow: hidden;

    &_item {
        transition: all 0.2s ease-in-out;

        &:hover {
            filter: drop-shadow(0 0 2px #d69292);
            cursor: pointer;
        }
    }

    &_container {
        width: 300px;
        padding: 10px;
        border: 2px solid #ffc4c4;
        border-radius: 15px;
        background-color: rgba(255, 255, 255, 0.64);
        overflow: hidden;
    }

    &_content {
        width: min(100% - 10px);
        height: 350px;
        border: 6px solid #ffc4c4;
        border-radius: 20px;
        overflow: hidden;
        background-color: white;

        &_img {
            width: calc(100% - 10px);
            padding: 5px;
            border-radius: 20px;
        }

        &_description {
            width: calc(100% - 40px);
            height: 30%;
            display: flex;
            align-items: center;
            padding: 0 20px 0 20px;
            font-size: 14px;
            text-align: center;
            text-decoration: none;
            color: black;
            font-family: 'Lato', sans-serif;
        }

        &_name {
            width: calc(100% - 6px);
            margin: 10px 0 10px 0;
            padding: 5px 0 5px 0;
            font-size: 20px;
            text-align: center;
            font-family: 'Lato', sans-serif;
            background-color: #ffc4c4;
            border-radius: 5px;
            border: 3px solid white;
        }
    }
}

.prototypes_diamond {
    top: -27%;
    &:before {
        background-color: #ffc4c4;
    }
}