.sectionModel {
    width: 100vw;
    height: 80vh;
    display: inline-flex;
    flex-direction: column;
    align-items: center;
    cursor: default;
}

.model_display {
    width: max(910px, 500px);
    display: inline;
    justify-content: center;

    &_container {
        display: grid;
        grid-template-columns: repeat(auto-fill, minmax(100px, 120px));
        place-items: center;
        display: flex;
        align-items: center;
        justify-content: center;
        gap: auto;
        padding: 10px;
        margin: 20px;
        border: 2px solid #ffc4c4;
        border-radius: 15px;
        background-color: rgba(255, 255, 255, 0.64);
    }

    &_content {
        width: 100%;
        border: 6px solid #ffc4c4;
        border-radius: 20px;
        background-color: white;

        &_img {
            height: 150px;
            padding: 5px;
            border-radius: 10px;
        }

        &_description {
            width: 80%;
            margin: 20px 10% 20px 10%;
            font-size: 20px;
            text-align: center;
            font-family: 'Lato', sans-serif;
        }
    }
}