.sectionIntro {
    width: 100%;
    height: 90vh;
    display: inline-flex;
    flex-direction: row;
    align-items: center;
}

.intro {
    &_img {
        width: 60%;
        z-index: -20;
    }

    &_description {
        width: 65%;
        margin-right: 5%;
        text-align: left;
        padding-left: 2%;
        border-left: 10px solid #ffc4c4;
    }

    &_subtitle {
        font-size: 6vw;
        font-family: 'Roboto Condensed', sans-serif;
    }
}