.sectionWebsites {
    width: 100vw;
    height: 90vh;
    display: inline-flex;
    flex-direction: column;
    align-items: center;
    cursor: default;
}

.websites_display {
    width: 500px;
    height: 500px;
    display: flex;
    justify-content: center;
    align-items: center;

    &_item {
        transition: all 0.2s ease-in-out;

        &:hover {
            filter: drop-shadow(0 0 2px #d69292);
            cursor: pointer;
        }
    }

    &_container {
        display: flex;
        height: 480px;
        justify-content: center;
        border: 2px solid #ffc4c4;
        border-radius: 15px;
        padding: 10px;
        background-color: rgba(255, 255, 255, 0.64);
    }

    &_content {
        width: calc(100%);
        display: inline-block;
        border: 6px solid #ffc4c4;
        border-radius: 20px;
        background-color: white;

        &_img {
            height: 340px;
            padding: 5px;
            border-radius: 20px;
        }

        &_description {
            width: 80%;
            color: black;
            margin: 20px 10% 20px 10%;
            font-size: 18px;
            text-align: center;
            font-family: 'Lato', sans-serif;
        }
    }

    &_arrows {
        width: 60px;
        height: 60px;
        cursor: pointer;
    }
}

.right_arrow {
    transform: scale(-1, 1);
}

  