.body {
    width: 100%;
    height: 90vh;
    cursor: default;
    background-image:
        linear-gradient(to right, grey 1px, transparent 1px),
        linear-gradient(to bottom, grey 1px, transparent 1px);
}

img {
    user-drag: none;
    -webkit-user-drag: none;
    user-select: none;
    -moz-user-select: none;
    -webkit-user-select: none;
    -ms-user-select: none;
    background-size: 100% 100%;
    //pointer-events: none;
}

.graphic {
    width: 100%;
    max-height: 100vh;
    position: fixed;
    bottom: 0px;
    left: 0;
    z-index: -10;
    -webkit-mask-image: -webkit-gradient(linear, left bottom, left top, from(rgba(0, 0, 0, 0)), to(rgba(0, 0, 0, 1)));
    mask-image: linear-gradient(to top, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0));
    opacity: 0.75;
}

.scroll {
    height: auto;
    overflow-y: scroll;
    scroll-snap-type: y mandatory;
}

.scroll section {
    scroll-snap-align: start;
    position: relative;
    scroll-margin: 100px;
}

.section_title {
    width: 50%;
    height: 4px;
    margin: 40px 0 40px 0;
    text-align: center;
    line-height: 1px;
    background:
        linear-gradient(to left,
            rgba(255, 255, 255, 1) 0%,
            rgba(86, 75, 75, 0) 50%,
            rgba(86, 75, 75, 0) 50%,
            rgba(255, 255, 255, 1) 100%) left bottom #564B4B no-repeat;
            
    &_text {
        font-size: 48px;
        font-family: 'Roboto Condensed', sans-serif;
        color: #564B4B;
    }
}

h span {
    background-image: linear-gradient(rgba(0, 0, 0, 0) 0%, white 40%, white 70%, rgba(0, 0, 0, 0));
    padding: 0 10px 0 10px;
    border-radius: 25px;
}

.diamond {
    width: 16px;
    height: 16px;
    display: inline-block;
    position: relative;
    vertical-align: top;
    margin-top: -5px;

    &:before {
        width: 16px;
        height: 16px;
        display: block;
        left: 0;
        top: 0;
        right: 0;
        bottom: 0;
        position: absolute;
        background-color: #564B4B;
        content: "";
        -webkit-transform: rotate(45deg);
        -webkit-transition: border-color 0.3s;
        -moz-transform: rotate(45deg);
        -moz-transition: border-color 0.3s;
        transform: rotate(45deg);
        
    }

    &_inner {
        width: 16px;
        height: 16px;
        display: inline-block;
        position: relative;
        margin-top: 5px;

        &:before {
            width: 8px;
            height: 8px;
            display: block;
            left: 4px;
            top: -1px;
            right: 0;
            bottom: 0;
            position: absolute;
            background-color: white;
            content: "";
            -webkit-transform: rotate(45deg);
            -webkit-transition: border-color 0.3s;
            -moz-transform: rotate(45deg);
            -moz-transition: border-color 0.3s;
            transform: rotate(45deg);
        }
    }
}

.top_arrow,
.top_arrow_more,
.back_arrow {
    width: 15px;
    height: 15px;
    position: fixed;
    border-right: 3px solid black;
    border-top: 3px solid black;
    transform: rotate(-45deg);
    margin-left: 95%;
    margin-top: 30px;
    cursor: pointer;
}

.top_arrow_more {
    bottom: 0;
    margin-bottom: 20px;
}

.back_arrow {
    margin-left: 50px;
    transform: rotate(-135deg);
    margin-top: 0px;
    position: absolute;
}

.space_divider {
    height: 10vh;
}

.arrow_row {
    width: 15px;
    display: flex;
    position: absolute;
    margin-top: 0px;
}

.arrow_instruct {
    font-size: 18px;
    text-align: left;
    margin-left: 15px;
    bottom: 10px;
}

.arrow {
    width: 15px;
    height: 15px;
    position: relative;
    border-right: 3px solid black;
    border-top: 3px solid black;
    transform: rotate(135deg);
    margin-left: 55px;
    margin-top: -10px;
}

.up_arrow {
    width: 15px;
    height: 15px;
    position: relative;
    border-right: 3px solid black;
    border-top: 3px solid black;
    transform: rotate(-45deg);
    margin-left: 55px;
    cursor: pointer;
}

.up_arrow_row {
    width: 15px;
    margin-top: -40px;
}

.sectionMore {
    width: 100vw;
    height: 100vh;
}

.case_study_intro {
    width: 100%;
    display: inline-flex;
    padding: 30px 0px 30px 0px;
    background-image: linear-gradient(to bottom, #ffc4c4, white);

    .proj {
        text-align: left;
        padding: 50px;

        .sum_title {
            font-size: 60px;
            line-height: 1;
            font-family: 'Lato', sans-serif;
            font-weight: 700;
        }

        &_sum {
            line-height: 2;
        }
    }
}

.case_study {
    width: 100%;
    display: inline-block;
    text-align: left;

    &_section {
        border-left: 5px solid #d97c7c;
        margin: 50px 120px 50px 50px;
        padding: 15px 0px 0px 30px;

        &_header {
            font-size: 40px;
            line-height: 1;
            font-family: 'Lato', sans-serif;
            font-weight: 700;
        }

        &_description {
            line-height: 2;
            width: 90%;
        }

        &_imageblock {
            display: flex;
            justify-content: center;
        }
    }
}

